import { Component, ElementRef, OnInit, ViewChild } from "@angular/core";
//import { BarcodeScanner, BarcodeScannerOptions } from '@ionic-native/barcode-scanner/ngx';
import { QRCodeModule } from "angular2-qrcode";
import { AjaxService } from "../../services/ajax.service";
import { serverUrl, app } from "../../../environments/environment";
import { AlertController, ModalController, Platform } from "@ionic/angular";
import { CommonService } from "../../services/common.service";
import { Router } from "@angular/router";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { AddCertificateComponent } from "./add-certificate/add-certificate.component";

@Component({
  selector: "app-certificate-generation",
  templateUrl: "./certificate-generation.page.html",
  styleUrls: ["./certificate-generation.page.scss"],
})
export class CertificateGenerationPage implements OnInit {
  @ViewChild("content", { static: false }) content: ElementRef;
  imeiSearch: FormGroup;
  encodedData: any;
  Certificate: any;
  show = false;
  certificatedata: any;
  companyId = localStorage.getItem("userName");
  data;
  myPlatform: any;
  renewalcount: any;
  TotalcertificateData: any;
  Apmlogo =
    "data:image/png;base64,";
  pdfDate: any;
  panic: any;
  constructor(
    private ajaxService: AjaxService,
    private modalController: ModalController,
    private commonService: CommonService,
    private router: Router,
    private formBuilder: FormBuilder,
    public platform: Platform
  ) {}
  public values;
  public level: "L" | "M" | "Q" | "H";
  public width: number;
  clickToDownload() {
    // this.generatedcertificate();

    var printWindow = window.open("height=1000,width=900");
    printWindow.document.write("<html>");
    // printWindow.document.write('</body>')
    printWindow.document.write(
      '<body style="background-image: url(https://kingstrackimages.s3.amazonaws.com/Company/apmlogo_watermarker.svg); background-repeat: no-repeat;background-size: 100% 85%;">'
    );
    var divContents = document.getElementById("pdf1").innerHTML;
    printWindow.document.write(divContents);
    printWindow.document.write("</body></html>");
    printWindow.print();
    printWindow.document.close();
  }
  async Edit() {
    const modal = await this.modalController.create({
      component: AddCertificateComponent,
      cssClass: "",
      componentProps: {
        value: this.imeiSearch.value.imeiNumber,
      },
    });
    modal.onDidDismiss().then(() => {
      this.submit();
    });
    return await modal.present();
  }
  submit() {
    this.show = false;
    if (this.imeiSearch.value.imeiNumber) {
      const url =
        serverUrl.web +
        "/global/get/certificate?imeiNo=" +
        this.imeiSearch.value.imeiNumber +
        "&dealerId=" +
        localStorage.getItem("companySuffix");
      this.ajaxService.ajaxGet(url).subscribe((res) => {
        if (JSON.stringify(res) == "{}" || res.certificatedetails == "-") {
          this.commonService.showConfirm(
            "Imei not available press add button to add a Certificate"
          );
          this.show = false;
        } else if (res.message == "Success") {
          this.Certificate = res.certificatedetails;
          this.renewalcount = JSON.parse(res.renewalCount);
          this.panic = JSON.parse(res.panicCount);
          this.TotalcertificateData = JSON.parse(this.Certificate);
          var detailofqrcode = [];
          detailofqrcode.push(
            this.TotalcertificateData.CenterName,
            "Chassis:" + this.TotalcertificateData.ChassisNo,
            "EngineNo:" + this.TotalcertificateData.EngineNo,
            "Reg.No:" + this.TotalcertificateData.RegistraionNo
          );
          this.data = detailofqrcode.toString();
          this.values = this.data;
          this.show = true;
        } else {
          this.commonService.showConfirm(res.message);
        }
      });
    } else {
      this.commonService.showConfirm("please check the Imei number");
    }
  }
  getBack() {
    this.router.navigateByUrl("/tabs-login/new-dashboard");
  }

  clear() {
    this.imeiSearch.patchValue({
      imeiNumber: "",
    });
    this.show = false;
  }
  ionViewWillEnter() {
    if (history.state) {
      this.imeiSearch.patchValue(history.state);
    }
    this.clear();
  }

  ngOnInit() {
    this.myPlatform = this.platform.platforms()[0];
    if (this.myPlatform == "tablet") {
      this.myPlatform = "desktop";
    }
    var now = new Date();
    var time = new Date().toLocaleTimeString();
    var day = ("0" + now.getDate()).slice(-2);
    var month = ("0" + (now.getMonth() + 1)).slice(-2);
    var today = now.getFullYear() + "-" + month + "-" + day;
    this.pdfDate = today + " " + time;
    this.imeiSearch = this.formBuilder.group({
      imeiNumber: ["", Validators.required],
    });
  }
}
